.modal .menu {
  position: absolute;
  min-height: 80vh;
  width: 70%;
  background: linear-gradient(
    to right bottom,
    rgba(255, 255, 255, 0.7),
    rgba(255, 255, 255, 0.3)
  );
  border-radius: 2rem;
  margin-left: 50%;
  transform: translate(-50%);
}

.close {
  float: left;
  box-shadow: inset 0 0 200px rgba(0, 0, 0, 0.5);
  width: 25px;
  height: 25px;
  border-radius: 50%;
  display: inline-flex;
  justify-content: center;
  align-items: center;
}
.head_text {
  width: 80%;
  margin-left: 5%;
}
.setting_body {
  margin-top: 2%;
  margin-left: 5%;
  width: 90%;
  height: 65vh;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: column;
}
.setting_item {
  width: 100%;
}
.folder_body {
  width: 90%;
  height: 70vh;
  margin-left: 5%;
  margin-top: 2%;
  display: flex;
  overflow: auto;
  flex-wrap: wrap;
  z-index: 1;
}
.root_icons {
  font-size: 36px;
  margin: 1%;
  display: inline-flex;
  flex-direction: column;
  align-items: center;
}
.folder_name {
  font-size: 12px;
}
.info_icons {
  margin: 1%;
  width: 5%;
  height: 5vh;
}
.info_body {
  width: 90%;
  height: 20vh;
  margin-left: 5%;
  margin-top: 2%;
  display: flex;
}
.info_message {
  width: 98%;
  margin: 1%;
  height: 40vh;
  background-color: rgba(100, 100, 150, 0.7);
  color: #fff;
  border-radius: 15px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.file-viewer {
  width: 100%;
  height: 80vh;
  background: linear-gradient(
    to right bottom,
    rgba(200, 200, 255, 0.7),
    rgba(255, 200, 200, 0.3)
  );
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 2;
}
.file-viewer-body {
  width: 50%;
  height: 50vh;
  border-radius: 15px;
  background: #fff;
}
.file-viewer-item {
  width: 100%;
  height: 40vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.display_image {
  width: 50%;
}
.terminal {
  margin-left: auto;
  margin-right: auto;
  width: 40%;
  height: 45vh;
  background-color: black;
  z-index: 1;
  color: #fff;
  display: flex;
}
.console {
  width: 95%;
  height: 35vh;
  background-color: transparent;
  color: #fff;
  padding: 1%;
}
.console-cmd {
  width: 95%;
  background-color: transparent;
  color: #fff;
}
