.menu {
  position: absolute;
  min-height: 80vh;
  width: 70%;
  background: linear-gradient(
    to right bottom,
    rgba(255, 255, 255, 0.7),
    rgba(255, 255, 255, 0.3)
  );
  border-radius: 2rem;
  margin-left: 50%;
  transform: translate(-50%);
}

.desktop {
  width: 100%;
  height: 93vh;
  background-image: url('../Assets/images/wallpaper.jpg');
  background-repeat: no-repeat;
  background-size: 100% 93vh;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.desktop_icons {
  width: 95%;
  height: 90vh;
  margin-left: 2.5%;
  margin-top: 2%;
}

.folders {
  width: 7%;
  height: 8vh;
  font-size: 40px;
  margin-top: 5%;
}

.folder_name {
  font-size: 12px;
}

.taskbar {
  width: 100%;
  min-height: 7vh;
  background: #aaa;
  display: inline-flex;
  justify-content: space-between;
  align-items: center;
}

.init_button {
  width: 4%;
  height: 100%;
  background-color: #fff;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 700;
  font-size: x-large;
}

.init_button:hover {
  border: 1px solid #191919;
}

.utility {
  width: 15%;
  height: 4vh;
  float: left;
}
.utility_date_info {
  float: left;
  margin: 1%;
}
.card-holder {
  position: absolute;
}

.desktop-card {
  width: 60%;
  height: 35vh;
  background: linear-gradient(
    to right bottom,
    rgba(255, 255, 255, 0.7),
    rgba(255, 255, 255, 0.3)
  );
  right: 10%;
  margin: 2%;
  padding: 2%;
  box-shadow: 1px 2px 5px #191919;
  overflow: auto;
}

.skew-1 {
  display: block;
  transform: rotate(10deg);
}
.skew-2 {
  display: block;
  transform: rotate(-10deg);
}
